'use client';

import { Container, Box, Typography, Paper, Button, IconButton } from '@mui/material';
import { useState } from 'react';
import RefreshIcon from '@mui/icons-material/Refresh';

export default function DashboardPage() {
  const [balance, setBalance] = useState(1000);
  const [income, setIncome] = useState(2500);
  const [expenses, setExpenses] = useState(1500);
  const [isRefreshing, setIsRefreshing] = useState(false);

  const handleRefresh = () => {
    setIsRefreshing(true);
    
    // Simulate API delay
    setTimeout(() => {
      // Generate random new values to simulate data refresh
      const newIncome = Math.floor(Math.random() * 5000) + 1000;
      const newExpenses = Math.floor(Math.random() * 3000) + 500;
      const newBalance = newIncome - newExpenses;
      
      setIncome(newIncome);
      setExpenses(newExpenses);
      setBalance(newBalance);
      setIsRefreshing(false);
    }, 1000);
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }} className="welcome-section">
        <Typography variant="h4" className="welcome-title">
          Welcome back, User!
        </Typography>
        <IconButton 
          onClick={handleRefresh} 
          aria-label="refresh data" 
          className={`refresh-button ${isRefreshing ? 'rotating' : ''}`}
          disabled={isRefreshing}
        >
          <RefreshIcon />
        </IconButton>
      </Box>
      
      <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(3, 1fr)' }, gap: 3, mt: 3 }}>
        <Paper className="dashboard-card balance-card" sx={{ p: 2, textAlign: 'center' }}>
          <Typography variant="h6">Total Balance</Typography>
          <Typography variant="h4">${balance.toLocaleString()}</Typography>
        </Paper>

        <Paper className="dashboard-card income-card" sx={{ p: 2, textAlign: 'center' }}>
          <Typography variant="h6">Income</Typography>
          <Typography variant="h4">${income.toLocaleString()}</Typography>
        </Paper>

        <Paper className="dashboard-card expense-card" sx={{ p: 2, textAlign: 'center' }}>
          <Typography variant="h6">Expenses</Typography>
          <Typography variant="h4">${expenses.toLocaleString()}</Typography>
        </Paper>
      </Box>

      <Box sx={{ mt: 3 }}>
        <Paper className="overview-card" sx={{ p: 2 }}>
          <Typography variant="h6" className="section-header">Spending Overview</Typography>
          <Box className="chart-container">
            <Typography variant="body2" className="chart-placeholder">
              Chart showing your spending patterns will be displayed here
            </Typography>
          </Box>
        </Paper>
      </Box>

      <Box sx={{ mt: 3 }}>
        <Paper className="overview-card" sx={{ p: 2 }}>
          <Typography variant="h6" className="section-header">Recent Transactions</Typography>
          <Box className="transactions-list">
            <Typography variant="body2" className="transactions-placeholder">
              Your recent transactions will appear here
            </Typography>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
} 